@import url(https://fonts.googleapis.com/css2?family=Rubik&family=Ubuntu:ital@1&display=swap);
.add_sales_container {
    width: 100%;
    background: #0080c0;
    padding: 5px;
    border-radius: 10px;
    display: flex;
    flex-flow: column;
    margin-bottom: 15px;
    align-items: center;
}

.add_sales_container label {
    margin-bottom: 7px;
    color: rgb(233, 233, 233);
    font-size: 19px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.add_sales_container input {
    margin-bottom: 10px;
    padding: 5px;
    font-size: 20px;
    outline: none;
    border-radius: 15px;
    border: none;
    width: 90%;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}

.add_sales_container .submit {
    margin-top: 20px;
    padding: 5px;
    background-color: rgb(252, 164, 0);
    font-size: 20px;
    border: rgb(190, 124, 0) 2px solid;
    color: #fff;
    border-radius: 15px;
    transition: 0.30s;
    cursor: pointer;
    width: 100%;
}

.add_sales_container .submit:hover {
    background-color: rgb(190, 124, 0);
}


body {
    width: 100%;
    background-color: rgb(22, 22, 22);
}

.container {
    width: 70%;
    margin: 90px auto 20px auto;
    background-color: rgb(71, 71, 71);
    border-radius: 15px;
    display: flex;
    flex-flow: column;
    padding: 15px;
    align-items: center;
}

@media (max-width: 576px) {
    .container {
        margin: 50px auto 20px auto;
        width: 82%;
    }
}
.chart {
    width: 100%;
    background: #0080c0;
    padding: 5px;
    border-radius: 10px;
    display: flex;
    margin-bottom: 15px;
    overflow: auto;
}
.chart_bar {
    display: flex;
    flex-flow: column;
    align-items: center;
    padding: 20px;
    height: 160px;
    border-radius: 20px;
    margin: auto;
}

.chart_bar .chart_inner {
    height: 100%;
    background-color: rgb(218, 218, 255);
    width: 15px;
    border-radius: 20px;
    display: flex;
    flex-flow: column;
    justify-content: end;
}

.chart_bar .chart_inner .chart_fill {
    background-color: rgb(44, 218, 0);
    border-radius: 20px;
    transition: 0.3s;
}
.sales_container {
    width: 100%;
    background: #0080c0;
    padding: 5px;
    border-radius: 10px;
    display: flex;
    flex-flow: column;
}
.filter {
    background-color: #3bbeff;
    border-radius: 10px;
    margin-bottom: 20px;
    width: 100px;
    padding: 5px;
    border: #00a5f8 2px solid;
    cursor: pointer;
    color: #fff;
}
.sale_card {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    min-height: 80px;
    background-color: #3bbeff;
    border-radius: 10px;
    margin-bottom: 10px;
    cursor: pointer;
    flex-flow: wrap;
}

.sale_card p {
    background-color: rgb(252, 164, 0);
    padding: 10px;
    text-align: center;
    border-radius: 10px;
    color: #fff;
}

@media (max-width: 576px) {
    .sale_card p {
        margin-bottom: 10px;
    }
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: 'Rubik', sans-serif;
  font-family: 'Ubuntu', sans-serif;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
